<template>
    <div class="my-container">
      <div class="time"><span style="color:red;">{{showTime}}</span></div>
        <div>
            <div class="flex-row align-items">
                <span class="text-nowrap mr10 font20"> :</span>
                <span class="font24">{{title}}</span>
            </div>
        </div>
        <!--  -->
        <Single 
        :isAnswer='isAnswer'
        :singleQuestion='singleQuestion'></Single>
        <!--  -->
        <Multiple 
        :isAnswer='isAnswer'
        :multipleQuestion='multipleQuestion'></Multiple>
        <!--  -->
        <Judgement 
        :isAnswer='isAnswer'
        :judgementQuestion='judgementQuestion'></Judgement>
        <!--  -->
        <Answer 
        :isAnswer='isAnswer'
        :answerQuestion='answerQuestion'></Answer>
        <div class="mt20">
            <el-button type="primary" @click="submit"></el-button>
            <el-button type="success" @click="goBack"></el-button>
        </div>
    </div>
</template>

<script>
import { mapGetters } from "vuex";
import { post, get } from "../util/http.js";
import { strSimilarity2Percent } from "../util/index.js";
export default {
  data() {
    return {
      // 
      title: "",
      // 
      singleQuestion: [],
      // 
      multipleQuestion: [],
      // 
      judgementQuestion: [],
      // 
      answerQuestion: [],
      isAnswer: true,
      time: 0
    };
  },
  methods: {
    async submit() {
      let flag = true;
      let answer = {};
      // 
      this.singleQuestion.forEach(item => {
        let message = null;
        if (!item.answer) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          answer[item.id] = item.answer;
        }
        item.message = message;
      });
      // 
      this.multipleQuestion.forEach(item => {
        let message = null;
        if (item.answer.length == 0) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          answer[item.id] = item.answer;
        }
        item.message = message;
      });
      // 
      this.judgementQuestion.forEach(item => {
        let message = null;
        if (!item.answer) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          answer[item.id] = item.answer;
        }
        item.message = message;
      });
      // 
      this.answerQuestion.forEach(item => {
        let message = null;
        if (!item.answer) {
          message = "";
        }
        if (message) {
          flag = false;
        } else {
          answer[item.id] = item.answer;
        }
        item.message = message;
      });
      if (flag) {
        let params = {};
        params.title = this.title;
        params.answer = answer;
        // 
        params.userName = this.userInfo.name;
        // id
        params.userId = this.userInfo._id;
        params.questionId = this.$route.params.id;
        params.answerTime = this.showTime;
        try {
          const result = await post("/api/questions/submitQuestion", params);
          if (result.statusCode == 200) {
            this.$message({
              type: "success",
              message: result.message,
              showClose: true
            });
            this.$router.back();
          } else {
            this.$message({
              type: "warning",
              message: result.message,
              showClose: true
            });
          }
        } catch (error) {
          this.$message({
            type: "error",
            message: error.toString(),
            showClose: true
          });
        }
      }
    },
    // 
    goBack() {
      this.$router.back()
    },
    initTime() {
      this.timer = setInterval(() => {
        this.time++;
      }, 1000);
    }
  },
  async created() {
    let { id } = this.$route.params;
    try {
      const result = await get("/api/questions/getQuestionById/" + id);
      if (result.statusCode == 200) {
        this.title = result.data.questionList.title;
        this.singleQuestion = result.data.questionList.singleQuestion;
        this.multipleQuestion = result.data.questionList.multipleQuestion;
        this.judgementQuestion = result.data.questionList.judgementQuestion;
        this.answerQuestion = result.data.questionList.answerQuestion;
        this.initTime();
      } else {
        this.$message({
          type: "warning",
          message: result.message,
          showClose: true
        });
      }
    } catch (error) {
      this.$message({
        type: "error",
        message: error.toString(),
        showClose: true
      });
    }
  },
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer);
    }
  },
  computed: {
    ...mapGetters(["userInfo"]),
    showTime() {
      let s = this.time % 60;
      s = s < 10 ? `0${s}` : s;
      let m = parseInt(this.time / 60) % 60;
      m = m < 10 ? `0${m}` : m;
      let h = parseInt(this.time / 60 / 60) % 24;
      h = h < 10 ? `0${h}` : h;
      return `${h}${m}${s}`;
    }
  },
  beforeRouteLeave(to, from, next) {
    let flag = true;
    // 
    flag &&
      this.singleQuestion.forEach(item => {
        if (item.answer) {
          flag = false;
        }
      });
    // 
    flag &&
      this.multipleQuestion.forEach(item => {
        if (item.answer.length != 0) {
          flag = false;
        }
      });
    // 
    flag &&
      this.judgementQuestion.forEach(item => {
        if (item.answer) {
          flag = false;
        }
      });
    // 
    flag &&
      this.answerQuestion.forEach(item => {
        if (item.answer) {
          flag = false;
        }
      });
    if (flag) {
      next();
    } else {
      this.$confirm(", ", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          next()
        })
        .catch(() => {});
    }
  }
};
</script>

<style lang="less" scoped>
.my-container {
  padding: 20px;
}
.min-width {
  min-width: 300px;
}
.time {
  position: fixed;
  right: 50px;
  top: 100px;
  font-size: 20px;
}
</style>


